body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px 0px;
  font-size: .9rem;
  min-height: 100vh;
  box-sizing: border-box;
  transition: color 400ms, background-color 400ms;
}

.App .circle {
  transition: background-color 400ms;
}

.App .title .o-fill {
  transition: background-color 400ms;
}

/* dark theme */
.App.dark {
  color: rgb(246, 246, 246);
  background-color: rgb(48, 48, 48);
}
.App.dark .circle{
  background-color: rgb(246, 246, 246);
}

.App.dark .title .o-fill {
  background-color: rgb(246, 246, 246);
}

.App.dark a {
  color: rgb(198, 198, 198);
  transition: color 200ms;
  text-decoration: none;
}

.App.dark a:hover {
  color: rgb(246, 246, 246);
}

/* light */
.App.light {
  background-color: rgb(246, 246, 246);
  color: rgb(48, 48, 48);
}
.App.light .circle{
  background-color: rgb(48, 48, 48);
}

.App.light .title .o-fill {
  background-color: rgb(48, 48, 48);
}

.App.light a {
  color: rgb(119, 119, 119);
  transition: color 200ms;
  text-decoration: none;
}

.App.light a:hover {
  color: rgb(48, 48, 48);
}
/*  */

.App .main-text-wrapper {
  margin: 50px 0 100px;
}

.App .main-text-wrapper, .App .footer {
  width: 320px;
}

.App .title .o-fill {
  display: inline-block;
  width: .93rem;
  height: .93rem;
  border-radius: 100%;
  margin: 0 .27rem 0 .22rem;
}

.App .title {
  cursor: pointer;
  margin-bottom: 25px;
  font-size: 1.25rem;
  font-weight: 600;
  letter-spacing: .2rem
}

.App .footer {
  font-size: .8rem;
}


@media (min-width: 576px) {
  .App {
    padding: 30px 20px;
  }
  .App .main-text-wrapper, .App .footer {
    width: 410px;
  }
}

.circle-wrapper {
  width: 12px;
  height: 12px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;
}

@media (min-width: 576px) {
  .circle-wrapper {
    width: 15px;
    height: 15px;
    margin: 13px;
  }
}

.circle-wrapper .circle {
  border-radius: 100%;
  -webkit-animation: pulse 1s linear infinite;
          animation: pulse 1s linear infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

@-webkit-keyframes pulse {
  from {
    height: 0;
    width: 0;
  }
  to {
    height: 100%;
    width: 100%;
  }
}

@keyframes pulse {
  from {
    height: 0;
    width: 0;
  }
  to {
    height: 100%;
    width: 100%;
  }
}

.Board {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Board .row {
  display: flex;
}

